.c4dropdown {
  position: relative;
}
.c4dropdown--default--styles {
  border-radius: 0.2rem;
  border: none;
  cursor: pointer;
}
.c4dropdown--default--styles--trigger {
  background-color: hsl(251, 16.9%, 12.7%);
  padding: 0.25rem;
  color: hsl(0, 0%, 100%);
  border-radius: 0.2rem;
}
.c4dropdown--default--styles--trigger:hover {
  background: hsl(251, 15.7%, 20%);
}
.c4dropdown--trigger {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  cursor: pointer;
}
.c4dropdown--trigger .c4dropdown--icon {
  fill: currentColor;
  display: inline-flex;
  padding: 0;
  margin-left: 5px;
  height: 100%;
}
.c4dropdown--trigger .c4dropdown--icon:hover {
  box-shadow: none;
}
.c4dropdown--trigger .c4dropdown--icon.c4dropdown--open {
  transform: rotate(-180deg);
  transition: transform 150ms ease;
}
.c4dropdown--listcontainer {
  visibility: hidden;
  position: absolute;
  top: 100%;
  right: 0;
  transition: all 300ms;
  width: 260px;
  background: transparent;
  padding-top: 0.5rem;
  transform: translateY(-15px);
  opacity: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.c4dropdown--listcontainer.c4dropdown--open {
  visibility: visible;
  opacity: 1;
  z-index: 1;
  transform: translateY(0);
}
.c4dropdown--listcontainer .c4dropdown--list {
  background: hsl(251, 16.9%, 12.7%);
  border-radius: 6px;
  width: 100%;
  display: flex;
  flex-direction: column;
  border: 1px solid hsl(253, 11.2%, 31.4%);
  overflow: hidden;
}
.c4dropdown--listcontainer .c4dropdown--list .c4dropdown--button {
  padding: 1rem;
  border: 0px;
  border-bottom: 1px solid hsl(249, 14%, 27%);
  color: hsl(0, 0%, 100%);
  background: hsl(251, 16.9%, 12.7%);
  text-align: left;
  margin: 0;
  padding: 15px;
  display: flex;
  align-items: center;
  font-weight: normal;
  text-transform: none;
  text-decoration: none;
}
.c4dropdown--listcontainer .c4dropdown--list .c4dropdown--button:last-of-type {
  border-bottom: 0px;
}
.c4dropdown--listcontainer .c4dropdown--list .c4dropdown--button[target=_blank]::after {
  content: "‚Üó";
  padding-left: 5px;
}
.c4dropdown--listcontainer .c4dropdown--list .c4dropdown--button:hover {
  background: hsl(251, 15.7%, 20%);
  color: white;
}
@media screen and (min-width: 960px) {
  .c4dropdown--listcontainer {
    left: unset;
    right: 0;
  }
